*{
    margin: 0;
    padding: 0; 
    box-sizing: border-box;
}

.container{
    padding: 1em;
    min-height: 50vh ;
    width: 100%;
    text-align: center;
}
.dropdown{
    padding:0.5em;
    margin: 1em 0;
}
.flex{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.box{
    width: 100%;
    font-size: 1.5em;
    margin: 1em;
    padding: 0.5em;
    border-radius: 8px;
}
.box.confirmed{
    border: 1px solid #c09b61;
    background: #f0bc81;
}
.box.recovered{
    border: 1px solid #95c458;
    background: #c0f081;
}
.box.deaths{
    border: 1px solid #c08c61;
    background: #f08e81;
}

@media screen and (min-width: 900px){
    .flex{
        flex-wrap: nowrap;
    }
    .box{
        padding: 2em 1em;
        max-width: 300px;
        line-height: 1.5;
    }
}